/* ==================================== */
/* -- Global Styles -- */
/* ==================================== */
/* Import CSS Leaflet: Penting untuk memuat style dasar Leaflet */
@import "~leaflet/dist/leaflet.css";

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Arial", sans-serif;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.container {
  max-width: 1300px;
  margin-inline: auto;
  padding-inline: 32px;
}

/* ==================================== */
/* -- Header & Navigation -- */
/* ==================================== */
header {
  background-color: lightskyblue;
  position: sticky;
  top: 0;
  z-index: 1000;
  /* Kriteria W1: Tambahkan view-transition-name untuk header */
  view-transition-name: header-bar;
}

.main-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 90px;
  padding-block: 20px;
  gap: 30px;
}

/* Brand/H1 Styling */
.brand-heading {
  margin: 0; /* Menghilangkan margin bawaan H1 */
}

.brand-name {
  font-size: 1.5rem;
  font-weight: bold;
  color: #333;
  text-decoration: none;
}

/* Drawer & Button */
.navigation-drawer {
  position: fixed;
  inset-block-start: 0;
  inset-inline-start: 0;
  min-height: 100vh;
  width: 250px;
  padding: 20px 10px;
  background-color: #fff;
  box-shadow: 0 0 4px 2px #00000033;
  transform: translateX(-100%);
  transition: transform 350ms ease-in-out;
  z-index: 9999;
}

.navigation-drawer.open {
  transform: translateX(0);
}

.drawer-button {
  display: inline-block;
  /* Hapus display di desktop */
  font-size: 28px;
  font-weight: bold;
  color: #141e16;
  padding: 8px;
  border: 0;
  border-radius: 4px;
  background-color: transparent;
  cursor: pointer;
}

/* Nav List */
.nav-list {
  list-style: none;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: 10px 20px;
}

.nav-list li a {
  display: block;
  padding: 10px 10px;
  color: #333;
  text-decoration: none;
  transition: background-color 0.2s;
}

.nav-list li a:hover,
.nav-list li a:focus {
  background-color: #f0f0f0;
  text-decoration: none;
}

/* ==================================== */
/* -- Main Content & Sticky Footer Setup -- */
/* ==================================== */
.main-content {
  flex-grow: 1; /* Penting untuk Sticky Footer */
  padding-block: 20px;
  /* Kriteria W1: Tambahkan view-transition-name untuk transisi konten utama */
  view-transition-name: main-content;
}

/* ==================================== */
/* -- Footer Styles -- */
/* ==================================== */
footer {
  background-color: #333;
  color: #fff;
  padding: 20px 0;
  font-size: 0.9rem;
}

.footer-content {
  text-align: center;
}

.footer-content p {
  margin-block: 5px;
}

/* ==================================== */
/* -- Kriteria 4: Aksesibilitas Styles -- */
/* ==================================== */

/* Skip Link */
.skip-link {
  position: absolute;
  top: -40px;
  left: 0;
  background-color: #ffc107;
  color: #333;
  padding: 8px;
  text-decoration: none;
  z-index: 9999;
}

.skip-link:focus {
  top: 0;
}

/* Skip to content anchor (diberi fokus oleh JS) */
.skip-content-anchor:focus {
  outline: 2px solid #007bff;
  outline-offset: 2px;
}

/* Status Messages & Loading */
.loading-indicator,
.status-message {
  margin: 15px auto;
  padding: 15px;
  border-radius: 5px;
  text-align: center;
  font-weight: bold;
}
.status-message.success {
  background-color: #d4edda;
  color: #155724;
  border: 1px solid #c3e6cb;
}
.status-message.error {
  background-color: #f8d7da;
  color: #721c24;
  border: 1px solid #f5c6cb;
}

/* ==================================== */
/* -- Kriteria 1: View Transition API Styles -- */
/* ==================================== */

/* Menggantikan style transisi manual dengan view-transition-name */

/* Efek kustom untuk transisi konten utama */
::view-transition-old(main-content),
::view-transition-new(main-content) {
  /* Pastikan elemen mengisi seluruh area snapshot */
  position: absolute;
  inset: 0;
}

/* Mengatur transisi yang lebih halus untuk konten */
::view-transition-old(main-content) {
  animation: fade-out 0.3s ease-out;
}

::view-transition-new(main-content) {
  animation: fade-in 0.35s ease-in;
}

/* Keyframes untuk efek fade dan slide kecil (optional, tapi bagus) */
@keyframes fade-in {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fade-out {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(-10px);
  }
}

/* ==================================== */
/* -- Kriteria 2: Home Page (Peta & List) -- */
/* ==================================== */
.page-title {
  text-align: center;
  margin-bottom: 20px;
  /* Kriteria W4: Pastikan page-title adalah h1 pada halaman utama */
}

.story-layout {
  display: grid;
  margin-top: 20px;
  gap: 30px;
}

/* Kriteria W4: Pastikan h3 di bawah ini mengikuti h2 di halaman, atau ganti levelnya */
.story-list-container h3,
.story-map-container h3 {
  margin-bottom: 15px;
}

/* Story List */
.story-list {
  list-style: none;
  padding: 0;
  display: grid;
  gap: 20px;
}

.story-item {
  border: 1px solid #ccc;
  border-radius: 8px;
  overflow: hidden;
  cursor: pointer;
  transition: transform 0.3s, box-shadow 0.3s, background-color 0.3s;
  outline-offset: 4px;
}

.story-item:hover,
.story-item:focus,
.story-item.highlighted {
  transform: translateY(-5px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background-color: #f5f5f5;
}

.story-card {
  display: flex;
  flex-direction: column;
}

.story-img {
  width: 100%;
  max-height: 200px;
  object-fit: cover;
  display: block;
}

.story-content {
  padding: 15px;
}

.story-name {
  margin-bottom: 5px;
}

.story-description {
  font-size: 0.9rem;
  color: #555;
  margin-bottom: 10px;
}

.story-location {
  display: inline-block;
  background-color: #e0f7fa;
  color: #00bcd4;
  padding: 2px 6px;
  border-radius: 4px;
  font-size: 0.75rem;
}

.button-add-story {
  display: inline-block;
  padding: 8px 15px;
  margin-bottom: 20px;
  background-color: #28a745;
  color: white;
  text-decoration: none;
  border-radius: 4px;
  font-weight: bold;
  transition: background-color 0.2s;
}

.button-add-story:hover {
  background-color: #218838;
}

/* Map */
.map-container {
  height: 400px;
  width: 100%;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.map-layer-control {
  margin-top: 15px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.map-layer-control h4 {
  margin-top: 0;
  margin-bottom: 8px;
  font-size: 1rem;
}

.map-layer-control label {
  display: block;
  margin-bottom: 5px;
  cursor: pointer;
}

/* ==================================== */
/* -- Kriteria 3: Add Story Form -- */
/* ==================================== */

.story-form {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  display: grid;
  gap: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
}

.story-form fieldset {
  padding: 15px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.story-form legend {
  font-weight: bold;
  padding: 0 10px;
  font-size: 1.1rem;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

.form-group input:not([type="radio"]),
.form-group textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  transition: border-color 0.2s;
}

.form-group input:focus,
.form-group textarea:focus {
  border-color: #007bff;
  outline: none;
}

/* Input Validation (Skilled +3 pts) */
.form-group input.is-invalid,
.form-group textarea.is-invalid {
  border-color: #dc3545;
}

.error-text {
  color: #dc3545;
  font-size: 0.85rem;
  margin-top: 5px;
  min-height: 18px;
}

/* File/Camera Group */
.file-group .photo-options {
  display: flex;
  gap: 10px;
}

.form-button {
  width: auto;
  padding: 10px 15px;
  border: none;
  border-radius: 4px;
  background-color: #007bff;
  color: white;
  text-align: center;
  text-decoration: none;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.2s;
}

/* Custom style untuk label input type file agar terlihat seperti tombol */
.file-group label[for="photo"] {
  display: block;
  padding: 10px 15px;
  background-color: #6c757d;
  color: white;
  text-align: center;
  border-radius: 4px;
  cursor: pointer;
  font-weight: normal;
}
.file-group label[for="photo"]:hover {
  background-color: #5a6268;
}

.form-button:hover:not(:disabled) {
  background-color: #0056b3;
}

#submit-button {
  width: 100%;
  padding: 12px;
  background-color: #28a745;
}

#submit-button:hover:not(:disabled) {
  background-color: #218838;
}

#submit-button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.file-name-display {
  margin-top: 10px;
  font-size: 0.9rem;
  color: #555;
}

.photo-preview-container {
  margin-top: 15px;
  text-align: center;
}

.photo-preview {
  max-width: 100%;
  height: auto;
  border-radius: 4px;
}

/* Camera Stream (Advance +4 pts) */
.camera-stream-container {
  margin-top: 15px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  text-align: center;
}

#camera-stream {
  max-width: 100%;
  height: auto;
  border: 1px solid #eee;
  margin-bottom: 10px;
}

#capture-button {
  background-color: #ffc107;
  color: #333;
  font-weight: bold;
}

#capture-button:hover {
  background-color: #e0a800;
}

.location-display {
  margin-top: 10px;
  font-style: italic;
  color: #555;
}

.map-field .map-container {
  margin-top: 10px;
  height: 300px;
}

/* ==================================== */
/* -- Kriteria 4: Tampilan Responsif (Skilled +3 pts) -- */
/* ==================================== */

/* Tablet: 768px */
@media screen and (min-width: 768px) {
  .story-layout {
    grid-template-columns: 2fr 3fr; /* List lebih kecil, Map lebih besar */
  }

  .story-list {
    grid-template-columns: repeat(
      auto-fill,
      minmax(250px, 1fr)
    ); /* 2 kolom di tablet */
  }
}

/* Desktop: 1024px */
@media screen and (min-width: 1024px) {
  .navigation-drawer {
    position: static;
    min-height: auto;
    width: auto;
    padding: 0;
    background-color: transparent;
    box-shadow: none;
    transform: translateX(0);
  }

  /* Kriteria W4: Pastikan .main-header tidak menyembunyikan drawer di desktop */
  .main-header {
    min-height: 70px; /* Sedikit lebih kecil di desktop */
  }

  .main-header .navigation-drawer {
    /* Mengubah flex-direction pada .main-header container agar navigasi sejajar */
    /* Namun karena navigation-drawer terpisah, penyesuaian dilakukan pada drawer-nya. */
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 30px;
  }

  /* Tambahkan style untuk menyembunyikan menu saat sudah di desktop */
  .navigation-drawer {
    /* Perlu di-override agar navigasi tetap terlihat */
    display: flex !important;
    /* Hapus bayangan dan posisi tetap yang mengganggu di desktop */
    position: static;
    box-shadow: none;
    transform: none;
  }

  .nav-list {
    flex-direction: row;
  }

  /* Perbaiki: Hapus style yang tidak perlu di sini */
  /* .auth-nav-list,
  .user-nav-list {
    flex-direction: row;
    gap: 0 20px;
    margin-left: 20px;
  } */

  .nav-list li a {
    display: inline-block;
    padding: 6px 10px;
    background-color: transparent;
  }

  .nav-list li a:hover,
  .nav-list li a:focus {
    background-color: transparent;
    text-decoration: underline;
  }

  .drawer-button {
    display: none;
  }

  .story-layout {
    grid-template-columns: 1fr 1fr; /* List dan Map sama besar */
  }

  .story-list {
    grid-template-columns: 1fr; /* Kembali ke 1 kolom di desktop */
  }
}
